.textOne {
  font-size: 20px;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .textOne {
    font-size: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .textOne {
    font-size: 35px;
  }
}

.textTwo {
  font-size: 17px;
  font-weight: 600;
  margin-top: 20px;
  padding: 10px 20px;
  border: 2px solid rgb(255, 4, 4);
  border-radius: 20px;
}

@media screen and (min-width: 768px) {
  .textTwo {
    font-size: 25px;
    margin-top: 40px;
  }
}

@media screen and (min-width: 1200px) {
  .textTwo {
    font-size: 28px;
    margin-top: 55px;
  }
}

.home {
  min-height: 100vh;
  padding-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .home {
    padding-top: 70px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .home {
    padding-top: 100px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
